<style>
	
</style>


<template>
	<div>
		<!--  -->
		<foodproductsnav></foodproductsnav>

		<!--  -->
		<pricesort></pricesort>				
		
		<!--  -->
		<foodproducts :message="bestsellgoods"></foodproducts>
	</div>
</template>


<script>
	import foodproductsnav from './foodproductsnav.vue'
	import pricesort from './../public/pricesort.vue'
	import foodproducts from './../public/foodproducts.vue'
	import { eventBus } from '@/eventBus'

	export default{
		name: 'foodwebproduct',
		data(){
			return {
				productgoods: [],
				bestsellgoods: [
					{
						goodspic: require('@/assets/goods_description_01.jpg'),
						goodsname: '',
						goodsclassify: '',
						marketprice: '5.90',
						presentprice: '4.90'
					},
					{
						goodspic: require('@/assets/goods_02.jpg'),
						goodsname: '  ',
						goodsclassify: '',
						marketprice: '5.90',
						presentprice: '4.80'
					},
					{
						goodspic: require('@/assets/goods_03.jpg'),
						goodsname: '    ',
						goodsclassify: '',
						marketprice: '9.70',
						presentprice: '8.30'
					},
					{
						goodspic: require('@/assets/goods_04.jpg'),
						goodsname: '  1',
						goodsclassify: '',
						marketprice: '9.70',
						presentprice: '8.20'
					},
					{
						goodspic: require('@/assets/goods_05.jpg'),
						goodsname: '  ',
						goodsclassify: '',
						marketprice: '9.70',
						presentprice: '8.00'
					},
					{
						goodspic: require('@/assets/goods_06.jpg'),
						goodsname: '    ',
						goodsclassify: '',
						marketprice: '9.70',
						presentprice: '8.80'
					},
					{
						goodspic: require('@/assets/goods_07.jpg'),
						goodsname: 'Richeese    ',
						goodsclassify: '',
						marketprice: '9.90',
						presentprice: '9.00'
					},
					{
						goodspic: require('@/assets/goods_08.jpg'),
						goodsname: '  )',
						goodsclassify: '',
						marketprice: '9.90',
						presentprice: '8.90'
					}
				]
			}
		},
		created(){
			this.productgoods = this.bestsellgoods
		},
		mounted() {
			// 
			eventBus.$on('change-message',() => {
				this.bestsellgoods = this.$root.bestsellgoods
			})
		},
		components: {
			foodproductsnav,
			pricesort,
			foodproducts
		}
	}
</script>


