<style scoped>
	.pricesort_mainnav{
		margin: 20px auto 0;
		width: 1153px;
		height: 32px;
		border: 1px solid #d4d4d4;
	}
	.pricesort_mainnav_price{
		float: left;
		width: 161px;
		height: 32px;
		line-height: 30px;
		border-right: 1px solid #d4d4d4;
		cursor: pointer;
	}
	.pricesort_mainnav_price_text{
		float: left;
		font-size: 12px;
		margin-left: 66px;
	}
	.pricesort_mainnav_price_sign{
		float: left;
		display: block;
		margin-left: 4px;
		margin-top: 11px;
		width: 7px;
		height: 8px;
		background-image: url(./../../assets/up.gif);
		cursor: pointer;
	}
	.pricesort_mainnav_selectedprice{
		float: left;
		width: 396px;
		height: 32px;
		text-align: center;
		line-height: 30px;
		border-right: 1px solid #d4d4d4;
	}
  .pricesort_mainnav_selectedprice input.inputpricecounts{
    width: 90px;
  }
	.pricesort_mainnav_searchgoods{
		float: left;
		width: 396px;
		height: 32px;
		text-align: center;
		line-height: 30px;
	}
	.pricesort_mainnav_searchgoods input.inputpricesearchgoods{
		width: 180px;
	}

  /*  */
  .change_price_sign{
    -webkit-transform: rotate(180deg);
    transform: rotate(180deg);
  }

  .fill_color_wrap{
    background-color: #d4d4d4;
  }
  .fill_color_wrap .pricesort_mainnav_price_text{
    color: #fff;
  }
</style>


<template>
  <div class="pricesort_mainnav">
    <div 
      class="pricesort_mainnav_price"
      @click="orderPrice('presentprice',original = true)"
      :class="upprice ? 'fill_color_wrap' : ''">
      <span class="pricesort_mainnav_price_text"></span>
      <span class="pricesort_mainnav_price_sign change_price_sign"></span>
    </div>
    <div 
      class="pricesort_mainnav_price" 
      @click="orderPrice('presentprice',original = false)"
      :class="downprice ? 'fill_color_wrap' : ''">
        <span class="pricesort_mainnav_price_text"></span>
        <span class="pricesort_mainnav_price_sign"></span>
    </div>
    <div class="pricesort_mainnav_selectedprice">
        <span class="rmb"></span>
        <input 
          class="inputpricecounts" 
          type="text"
          v-model="searchminprice">
        <span class="dash">-</span>
        <span class="rmb"></span>
        <input 
          class="inputpricecounts" 
          type="text"
          v-model="searchmaxprice">
        <button @click="orderPriceRange()"></button>
    </div>
    <div class="pricesort_mainnav_searchgoods">
      <span></span>
      <input
        class="inputpricesearchgoods" 
        type="text"
        v-model="searchproducts">
      <!-- <button @click="orderNameRange()"></button> -->
    </div>
  </div>
</template>


<script>
  import { eventBus } from '@/eventBus'

	export default{
		data(){
			return {
				message: this.$root.bestsellgoods,
				searchminprice: '', // 
				searchmaxprice: '', // 
				searchproducts: '', // 
        upprice: false, // 
        downprice: false, // 
        original: '',   //
        arrfoodproducts: [], //search
        origialarray: [] // 
			}
		},
    created() {
      // origialarray
      this.origialarray = this.$root.bestsellgoods 
    },
    watch: {
      // 
      searchproducts(){
        this.arrfoodproducts = [];//
        for (var i=0;i<this.$root.bestsellgoods.length;i++){
          //forname
          if(this.$root.bestsellgoods[i].goodsname.search(this.searchproducts) != -1){
            //
            this.arrfoodproducts.push(this.$root.bestsellgoods[i]);
          }
        }
        // 
        if(this.searchproducts == ''){
          if(this.searchminprice == '' && this.searchmaxprice == ''){
            this.$root.bestsellgoods = this.origialarray
          }else{
            this.orderPriceChecked(this.origialarray)
            eventBus.$emit('change-message')
          }
        }else{
          // message$root
          this.$root.bestsellgoods = this.arrfoodproducts;
        }
        // 
        this.upprice = false;
        this.downprice = false;
        // change-message
        eventBus.$emit('change-message')
        return this.arrfoodproducts
      }
    },
		methods: {
      // 
      orderPriceChecked(array){
        // 
        var minprice = Number(this.searchminprice).toFixed(2),
            maxprice = Number(this.searchmaxprice).toFixed(2);
        var arrfoodproductsprice = []; // 
        var presentprice; // 

        if(minprice != 0.00 && maxprice == 0.00){
          // max
          maxprice = 100.00;
          console.log('max')
        }else if(minprice == 0.00 && maxprice != 0.00){
          // min
          minprice = 0.00
          console.log('min')
        }
        for (var i=0;i < array.length;i++){
          presentprice = Number(array[i].presentprice).toFixed(2);
          if( presentprice>=minprice && presentprice<=maxprice){
            //
            arrfoodproductsprice.push(array[i]);
          }
        }
        // message$root
        this.$root.bestsellgoods = arrfoodproductsprice;
      },
      // 
			orderPrice(letter , original){
        if(letter != ''){
            this.$root.bestsellgoods.sort((a,b) => {
                if(original){
                  this.upprice = true;
                  this.downprice = false;
                  return b[letter] - a[letter];
                }else{
                  this.upprice = false;
                  this.downprice = true;
                  return a[letter] - b[letter];
                }
            });
        }
        // change-message
        eventBus.$emit('change-message') 
      },
      // 
      orderPriceRange(){
        var presentprice; // 
        var minprice = Number(this.searchminprice).toFixed(2),
            maxprice = Number(this.searchmaxprice).toFixed(2);
        // 
        if(minprice == 0.00 && maxprice == 0.00){
          // min
          if(this.searchproducts == ''){
            this.$root.bestsellgoods = this.origialarray
            console.log('')
            console.log(this.origialarray)
          }else{
            this.$root.bestsellgoods = this.arrfoodproducts
            console.log('')
            console.log(this.arrfoodproducts)
          }
        }else{
          console.log('log in')
          if(this.searchproducts == ''){
            this.orderPriceChecked(this.origialarray)
          }else{
            this.orderPriceChecked(this.arrfoodproducts)
          }
        }
        // 
        this.upprice = false;
        this.downprice = false;
        eventBus.$emit('change-message')
      }
		}
	}
</script>


